button{
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
   font-size: 14px;
   margin-left: 30px;
  margin-top: 12px;
}
.Navbartext{
    color:black 
}
.Navbartexts{
    font-size: 6px;
}
.navbarlogo{
    margin-left: 0px;
margin-top: -10px;
}
.navbar{
    margin-left: 70px;
}
.logo1{
    margin-left:10px;
}
.logo2{
    margin-left: 10px;
}
.logo3{
    margin-left: 10px;
}

@media screen and (max-width: 1024px){
    .navbarlogo{
       margin-left: 330px;
       position: fixed;
    }
    .logo1{
        right: 10px;
        position:fixed;
        justify-content: center;
        margin-top: -10px;
    }
    .logo2{
        right: 60px;
        position:fixed;
        margin-top: -10px;
    }
    
    .logo3{
        display: none;
    }
}
